/* === Reset + Base === */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html, .App {
  height: 100%;
  font-family: 'Neue Haas Grotesk', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 400;
  background-color: #080808;
  color: white;
  overflow-x: hidden;
  overflow-y: auto; 
}

/* === Navbar === */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 48px;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 27px;
  z-index: 10;
}

.nav-logo {
  padding: 0; /* Removes extra space around the logo */
}
.nav-logo img {
  width: 220px; /* Adjust the width to make it smaller */
  height: auto; /* Maintain the aspect ratio */
  margin-left: -50px;
  margin-top:36px
}

.nav-right {
  display: flex;
  gap: 20px;
}

.nav-link {
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-size: 0.9rem;
  transition: transform 0.3s ease, opacity 0.3s ease, color 0.3s ease;
  margin-right:20px
}

.nav-link:hover {
  opacity: 0.6;
  transform: scale(1.1);

}

/* === Hero Section (Video Background) === */
.hero-section {
  position: relative;
  width: 100%;
  height: 90vh;
  overflow: hidden;
}

.background-video {
  position: absolute;
  top: 8%;
  left: 3%;
  width: 94%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transform: scale(1.08);
  animation: heroVideoReveal 0.9s ease-out forwards;
  animation-fill-mode: forwards;
}

.hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  opacity: 0;
  animation: heroTextReveal 1.1s ease-out forwards;
  animation-delay: 0.2s;
  animation-fill-mode: forwards;
}

@keyframes heroVideoReveal {
  0% {
    opacity: 0;
    transform: scale(1.08);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes heroTextReveal {
  0% {
    opacity: 0;
    transform: translate(-50%, -60%) scale(0.95);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

.hero-text h1 {
  font-size: 2.5rem;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-weight: 800;
  letter-spacing: 2px;
}

.hero-text img {
  width: 850px; /* Adjust the width to make it smaller */
  height: auto; /* Maintain the aspect ratio */
  margin-left:0px;
  margin-top:150px
}

