/* === Bio Section === */
/* === Info Section Layout === */
.info-section {
  background-color: #080808;
  color: white;
  padding: 121px 40px;
  margin-top: -47px;
  display: flex;
  justify-content: center;
}

/* Full container â€” keeps both text + image aligned */
.bio-container {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* push content left */
  max-width: 1300px;
  width: 100%;
  gap: 60px;
  margin-left: 140px;
}

/* The dark text box */
.info-content {
  flex: 0 0 55%; /* controls width of the text column */
  background: transparent;
  padding: 2.5%;
  opacity: 0;
  transform: translateY(30px);
  transition: transform 0.8s ease-out, opacity 0.8s ease-out;
  border:1px solid #555;
  border-radius: 5%;
}

.info-content.visible {
  opacity: 1;
  transform: translateY(0);
}

/* The photo on the right */
.info-image {
  flex: 1;
  display: flex;
  justify-content: center;
  position: relative;
  opacity: 0;
  transform: translateY(30px);
  transition: transform 0.8s ease-out, opacity 0.8s ease-out;
  overflow: hidden;
}

.info-image.visible {
  opacity: 1;
  transform: translateY(0);
}
.info-image img {
  width: 90%;
  max-width: 320px;
  border-radius: 0;
  object-fit: cover;
  opacity: 0.9;
}

/* Text inside the box */
.info-content h2 {
  font-size: 2.0rem;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-weight: 800;
  margin-bottom: 20px;
  text-align: center

}

.info-content p {
  font-size: .8rem;
  line-height: 1.6;
  color: #ddd;
  margin-bottom: 15px;
  letter-spacing: 0.03em;
  line-height: 1.7;
  
}

.info-content a {
  color: #ddd;
  text-decoration: none;
  border-bottom: 1px solid #ddd;
}

.info-content a:hover {
  opacity: 0.6;
}


.button-container {
  display: flex;
  gap: 20px; /* Adds spacing between buttons */
}

.button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007BFF; /* Blue background */
  color: white;
  text-decoration: none;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

.footer {
  background-color: #080808; /* Dark background */
  color: white;
  text-align: center;
  padding: .4px 0;
  width: 100%; /* Ensure it spans the full width */
  margin-top: 40px; /* Add spacing from the content above */
}

.footer .social-link {
  margin: 0 10px; /* Spacing between icons */
  display: inline-block;
}

.footer .social-link img {
  width: 30px; /* Icon size */
  height: auto; /* Maintain aspect ratio */
  transition: transform 0.3s ease; /* Add hover effect */
}

.footer .social-link img:hover {
  transform: scale(1.2); /* Slightly enlarge on hover */
}

.footer .social-link .logo-tiktok {
  width: 67px; 
  height: auto; /* Maintain aspect ratio */
  position: relative;
}

.footer .social-link .logo-yt {
  width: 32px; 
  height: auto; /* Maintain aspect ratio */
  position: relative;
  margin-left: -10px;
  top: -21px;
}
.footer .social-link .logo-ig {
  width: 20px; 
  height: auto; /* Maintain aspect ratio */
  position: relative;
  margin-left: -10px;
  top: -24px;
}

.page {
  min-height: 100vh;
  background-color: #080808;
  color: white;
  padding: 120px 20px 80px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.page-content {
  max-width: 640px;
  width: 100%;
}

.page-content--work {
  max-width: 1200px;
}

.page--work h1,
.page--contact h1 {
  width: 100%;
  text-align: center;
}

.contact-content {
  text-align: center;
  opacity: 0;
  animation: contactFade 0.9s ease-out forwards;
  animation-delay: 0.2s;
}

.work-grid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
  gap: 36px;
}

.work-card {
  position: relative;
  background-color: #0f0f0f;
  border: 1px solid rgba(255, 255, 255, 0.08);
  padding-top: 62%;
  overflow: hidden;
  opacity: 0;
  transform: translateY(35px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.work-card.visible {
  opacity: 1;
  transform: translateY(0);
}

.work-card__label {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 10px 14px;
  border-bottom-right-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.work-card__title {
  font-size: 0.85rem;
  font-weight: 700;
  letter-spacing: 0.08rem;
}

.work-card__roles {
  font-size: 0.7rem;
  color: #b19a8d;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

.work-card__frame {
  position: absolute;
  inset: 0;
}

.work-card__frame iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.footer--inline {
  margin-top: 60px;
}

@keyframes contactFade {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-section {
  position: relative;
  width: 100%;
  height: 90vh;
  overflow: hidden;
}

.hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  opacity: 0;
  animation: heroTextReveal 1.1s ease-out forwards;
  animation-delay: 0.2s;
  animation-fill-mode: forwards;
}
